@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
@font-face {
  font-family: pixelFont;
  src: url(/static/media/PressStart2P.74496d90.ttf);
}
* {
  font-family: pixelFont, sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

 .home-wrapper{
    height: auto;
    /* background:linear-gradient(180deg, #CFFF0F 12.58%, #5AC727 100%); */
    background: #F771A6;
    padding-top: 0px;
    min-height:100vh;
    display: flex;
 }
 .story-wrapper{
    height: auto;
    /* background:linear-gradient(180deg, #CFFF0F 12.58%, #5AC727 100%); */
    background: #F771A6;
    min-height:100vh;
    /* display: flex; */
    padding:0px 40px;
 }

 .text-wrapper{
     min-width:50%;
     height:100vh;
     display: flex;
     padding:0px 40px 0px 40px;
     align-items: center;
     flex-direction: column;
     justify-content:center;
    /* padding-top: 80px; */
     /* background-color: ; */
 }
 .pic-wrapper{
    min-width:50%;
    height:auto;
    display: flex;  
    align-items: center;  
    justify-content: center; 
 }


 @media only screen and (max-width: 800px) {
    .home-wrapper{
        height: auto;
        /* background:#7BD6FF; */
        padding-top: 0px;
        min-height:100vh;
        display: block;
     }
     .pic-wrapper{
      min-width:50%;
      height:auto;
      display: flex;  
      align-items: center;  
      justify-content: center; 
      padding:30px 0px;
   }
     .secondary-text-wrapper{
      min-height: auto;
height: auto;
padding-top:40px;
padding-bottom: 40px;
     }
  }
